@import url(https://fonts.googleapis.com/css?family=Cutive+Mono|Share+Tech+Mono|Ubuntu);
body {
  background: url(/static/media/stars.f2d3cda4.svg);
  margin: 0;
  padding: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  /* font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace; */
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  background-position: bottom;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;
  font-family: 'Share Tech Mono', monospace;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#cursor {
  fill: white;
  -webkit-animation: blink 1s infinite;
          animation: blink 1s infinite;
  
}

@-webkit-keyframes blink {
  0% { opacity: 1; }
  50% { opacity: 1; }
  51% { opacity: 0; }
  100% { opacity: 0; }
}

@keyframes blink {
  0% { opacity: 1; }
  50% { opacity: 1; }
  51% { opacity: 0; }
  100% { opacity: 0; }
}

* { text-decoration: none;}

.typewrite > .wrap { 
  border-right: 0.08em solid #fff;
  -webkit-animation: blink2 1s infinite;
          animation: blink2 1s infinite;
  box-sizing: border-box;
}

.typewrite {
  display: block;
}

.highlight {
  
}

@-webkit-keyframes blink2 {
  0% { border-right: 0.08em solid #fff; }
  50% { border-right: 0.08em solid #fff; }
  51% { border-right: none}
  100% { border-right: none }
}

@keyframes blink2 {
  0% { border-right: 0.08em solid #fff; }
  50% { border-right: 0.08em solid #fff; }
  51% { border-right: none}
  100% { border-right: none }
} 
